
.el-table_1_column_7 .cell{
        text-align: center;
}
.el-input__wrapper{
        padding: 0 !important;
}
th .cell{
        font-size: 11px;
}
.product-footer-pagination{
        display: flex;
        justify-content: center;
        align-content: center;
}
.btn-prev, .btn-next{
        margin-top: -6px !important;
}
.el-input__inner{
        background: white !important;
}
.pagination-footer-count-text{
        font-size: 12px;
}
.el-badge__content{
        font-size: 10px !important;
}
.price-column .el-input-number__decrease, .price-column .el-input-number__increase{
        display: none !important;
}
.price-column > .cell > .el-input-number{
        width: 80px !important;
}
.page-length{
        width: 65px;
        float: right;
}
.show-icon svg{
        height: 14px !important;
}
.price-list-select{
        width: 280px;
}

